$white: #eeeeee;
$blue: #1d3755;
$green: #01af9d;
$grey: #fff;


body {
    position: relative;
    min-height: 100vh;
    height: 100%;
    background-image: url("../img/homepage.png");
    background-size: 100%;
    background-position: top;
    background-repeat: no-repeat;
    margin: 0;
    padding: 0;
    font-size: 16px;
    font-family: 'Lato', sans-serif;
}
.home{
  display: block;
}
header {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 20px 50px;
}

nav {
    display: flex;
    align-items: center;
    a {
        font-size: 1.250em;
        margin: 0 15px;
        color: $white;
        text-decoration: none;
    }

}

h1 {
    color: $white;
    font-weight: 400;
    font-size: 2.500em;
}

a {
    cursor: pointer;
}

.compagny {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 80px;
}

.search {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 50px;

    input {
        border: none;
        background-color: $white;
        width: 500px;
        border-radius: 5px 0 0 5px;
        padding-left: 20px;
        font-size: 1em;
    }
}

button {
    display: flex;
    justify-content: center;
    background-color: $green;
    padding: 0 20px;
    border-radius: 0px 5px 5px 0px;
    color: $white;
    font-weight: 500;
    font-size: 1em;
    border: none;
    cursor: pointer;
}

footer {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    min-height: 171px;
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    padding: 30px 50px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    img{
        position: absolute;
        bottom: 0;
        right: 0;
        left: 0;
        width: 100%;
    }
    p {
        color: $blue;
        margin: 0;
        z-index: 1;

    }
    div{
        z-index: 5;
    }
    a {

        color: #c5c5c5;
        display: block;
        margin: 5px 0;
        text-decoration: underline;
    }
}


.title {
    max-width: 1000px;
    margin: auto;
    padding: 0 20px;
}

.white {
    color: $white;
}

h2 {
    font-size: 1.875em;
    color: $blue;
}

h3 {
    font-size: 1.250em;
    color: $green;
    text-transform: uppercase;
}

.container-result {
    margin-top: 200px;
    position: relative;
    background-color: $white;
    width: 100%;

    img:first-child {
        position: absolute;
        top: -170px;
        height: 170px;
        width: 100%;
    }
}
.article{
    position: relative;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    padding: 20px;
    max-width: 1000px;
    margin: auto;
}
.text{
   max-width: 600px;
}
.info-result{
    width: 280px;
    min-height: 250px;
    background-color: $blue;
    border-radius: 5px;
    padding: 15px;
    color: $white;
    font-size: 1.3em;
    p{
        margin: 0;
        &:nth-child(1) {
            margin-bottom: 50px;
        }
        &.green{
            margin: 20px 0;
        }
    }


}
.green{
    color: $green;
}
.flex{
    display: flex;
    justify-content: space-around;

    .line{
        height: 100px;
        background-color: $white;
        width: 1px;
    }
}
.background{
    width: 100%;
    position: absolute;
    bottom: -100px;
    z-index: 1;
    height: 100Px;
}
.info-compagny{
    margin-top: 0px;
    background-image:linear-gradient($blue, #136470);
    display: flex;
    justify-content: center;
    padding: 50px;
    min-height: 100vh;
    align-items: center;
    padding-top: 150px;
    position: relative;

    .flex{
        align-items: center;
        padding: 50px;
        background-color: $white;
        border-radius: 5px;
        .line{
            background-color: $blue ;
        }
        div{
            margin: 20px;
            p{
                min-width: 300px;
            }
        }
    }
}



